import { useContext } from "react";
import SearchBarContext from "./SearchBarContext";
import React from "react";

const ComponentProviderConsumer = () => {
  const [contextValue, setContextValue] = useContext(SearchBarContext);

  return (
    <>
      <input
        name="Setcontext"
        placeholder="setContextValue"
        value={contextValue}
        onChange={(event) => setContextValue(event.target.value)}
      />
    </>
  );
};
export default ComponentProviderConsumer;
